@import url(//fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(//beta.adalab.es/resources/stylesheets/all.css);
.filterCities{list-style:none}.filterCities .input{display:inline-block;margin-right:0.5rem}

*{box-sizing:border-box;margin:0;padding:0;text-decoration:none;list-style:none}:root{font-size:16px;font-size:"Open Sans",sans-serif}.page{background-color:aqua;width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;-webkit-flex-wrap:wrap;flex-wrap:wrap}.page__wrapper{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin-left:1.5em;margin-right:1.5em}@media screen and (min-width: 768px){.page__wrapper{-webkit-flex-direction:row;flex-direction:row}}

.header{display:-webkit-flex;display:flex;height:auto;-webkit-justify-content:center;justify-content:center;margin-bottom:15px;display:flex;-webkit-align-items:center;align-items:center}.header__img{width:4em}@media screen and (min-width: 768px){.header__img{width:390px}}.header__title{font-size:2em;margin-right:25px;color:#137191}@media screen and (min-width: 768px){.header__title{font-size:1.1em}}

.footer{width:100%;height:110px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;border-top:1px solid #137191;margin-top:20px;bottom:0;font-size:1.3em}@media screen and (min-width: 768px){.footer{font-size:1.6em}}.footer__small{color:#137191;margin:20px}.footer__logo{height:50px;margin:30px}@media screen and (min-width: 768px){.footer__logo{height:100px;margin:50px}}@-webkit-keyframes beats{from{-webkit-transform:none;transform:none}50%{-webkit-transform:scale(1.4);transform:scale(1.4)}to{-webkit-transform:none;transform:none}}@keyframes beats{from{-webkit-transform:none;transform:none}50%{-webkit-transform:scale(1.4);transform:scale(1.4)}to{-webkit-transform:none;transform:none}}.footer__heart{display:inline-block;font-size:20px;-webkit-animation:beats 0.5s infinite;animation:beats 0.5s infinite;color:#01a8c5}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Roboto", sans-serif}

